import React from 'react';
import './style.scss';

import Service from './Service';

import administratifs from '../../../../assets/images/administratif.jpeg';
import rps from '../../../../assets/images/rps.jpg';
import security from '../../../../assets/images/security.jpg';
import certificate from '../../../../assets/images/certificate.jpg';

class Services extends React.Component<{}, { services: Array<{name: string, image: string}> }> {
    constructor(props: {} | Readonly<{}>) {
        super(props);
        this.state = {
            services: Array(4).fill(null),
        };
    }

    render() {
        const services = this.state.services.slice();
        services[0] = {name: 'Sécurité', image: security};
        services[1] = {name: 'RPS', image: rps};
        services[2] = {name: 'Certifications', image: certificate};
        services[3] = {name: 'Services Administratifs', image: administratifs};

        const servicesMap = services.map((service) => {
            return (
                <Service name={service.name} image={service.image} />
            );
        });

        return (
            <section className='services'>
                <h2 className="title">Mes Services</h2>
                <div className="services-list">{servicesMap}</div>
            </section>
        );
    }
}

export default Services;