import React from 'react';
import './style.scss';

import { LazyLoadImage } from 'react-lazy-load-image-component';

import about from '../../../../assets/images/about.png';
import aboutMobile from '../../../../assets/images/about-mobile.png';
import aboutPixelized from '../../../../assets/images/about-pixelized.png';
import aboutMobilePixelized from '../../../../assets/images/about-mobile-pixelized.png';

class About extends React.Component<{}, { image: {full: string, pixelized: string} }> {
    constructor(props: {} | Readonly<{}>) {
        super(props);
        this.state = {
            image: {full: about, pixelized: aboutPixelized},
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resize.bind(this));
        this.resize();
    }

    resize() {
        this.setState({image: window.innerWidth <= 550 ?
            {full: aboutMobile, pixelized: aboutMobilePixelized} :
            {full: about, pixelized: aboutPixelized}});
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resize.bind(this));
    }

    render() {
        return (
            <section className='about'>
                <div className="about-container">
                    <h2 className="title">Un seul objectif</h2>
                    <h3 className="sub-title">Aider les artisans, les petites entreprises à gérer des problématiques grâce à mes différents domaines de compétences</h3>
                    <div className="description">
                        <div className="text">
                            <p>Titulaire de plusieurs diplômes (Assistante de Direction, Assistante Ressources Humaines, Préventeur en Santé et Sécurité), ma vie professionnelle depuis 20 ans riche en apprentissage m'a conduit à voler de mes propres ailes et à vous proposer un service de qualité et qui réponde à vos besoins. J'ai travaillé dans des sociétés très différentes les unes des autres (industrie, fonction publique, centre de formations, …) qui m'ont apportées des compétences solides.</p>
                            <p>Parallèlement aux services que je propose, je suis également formatrice dans les secteurs de la sécurité. Cette fonction supplémentaire me permet d'acquérir des retours d'expérience terrain dans des entreprises variées.</p>
                            <p>Je suis à votre écoute pour étudier avec vous vos problématiques, n'hésitez pas à me contacter.</p>
                        </div>
                        <LazyLoadImage
                            alt='Amandine Papon - about'
                            placeholderSrc={this.state.image.pixelized}
                            src={this.state.image.full}
                            effect='blur'
                            className='description-photo'
                        />
                    </div>
                </div>
            </section>
        );
    }
}

export default About;