import React from 'react';
import './style.scss';
import logo from '../../assets/images/logo.png';
import logoPixelized from '../../assets/images/logo-pixelized.png';

import { Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';

class Header extends React.Component<{}, { limit: boolean }> {
    constructor(props: {} | Readonly<{}>) {
        super(props);
        this.isOverLimit();
        this.state = {
            limit: false,
        };
    }

    isOverLimit() {
        window.onscroll = () => {
            const limit = this.state.limit;
            let value = null;
            if (window.pageYOffset >= 20) {
                value = true;
            } else if (window.pageYOffset < 20) {
                value = false;
            }
            if (value !== null && value !== limit) {
                this.setState({limit: value});
                console.log('set to '+value);
            }
        }
    }

    render() {
        return (
            <header className={this.state.limit ? 'menu menu-fix' : 'menu'}>
                <ul className='items left'>
                    <li>
                        <Link to='/services' className='link-white'>Services</Link>
                    </li>
                    <li>
                        <Link to='#about' className='link-white'>À propos</Link>
                    </li>
                    <li>
                        <Link to='#contact' className='link-white'>Contact</Link>
                    </li>
                </ul>
                <div className='items right'>
                    <Link to='/'>
                        <LazyLoadImage
                            alt='Amandine Papon - logo'
                            height='70'
                            placeholderSrc={logoPixelized}
                            src={logo}
                            effect='blur'
                        />
                    </Link>
                </div>
            </header>
        );
    }
}

export default Header;
