import React from 'react';
import './style.scss';

import { Link } from 'react-router-dom';

function Service(props: {name: string, image: string}) {
    return (
        <Link to={`/services#${props.name}`} className="card link-white" style={{backgroundImage: `url(${props.image})`}}>
            <div className="card-content">
                <span>{props.name}</span>
            </div>
        </Link>
    )
}

export default Service;