import React from 'react';
import './style.scss';

import banner from '../../../../assets/images/banner.jpeg';
import bannerPixelized from '../../../../assets/images/banner-pixelized.jpeg';

import { LazyLoadImage } from 'react-lazy-load-image-component';

class Banner extends React.Component {
    render() {
        return (
            <section className='banner'>
                <div className="container-image">
                    <LazyLoadImage
                        alt='Amandine Papon - banner'
                        placeholderSrc={bannerPixelized}
                        src={banner}
                        effect='blur'
                        className='banner-photo'
                    />
                </div>
                <div className="container-title">
                    <h1 className="title">Amandine PAPON</h1>
                    <h2 className="sub-title">Consultante et Formatrice en Santé et Sécurité au travail</h2>
                    <h3 className="sub-title">Certifications et gestions administrative</h3>
                </div>
            </section>
        );
    }
}

export default Banner;